import { mapState } from 'vuex'
export default {

	name: 'purchase',

	props: {},

	data() {
		return {
			active:0
		};
	},

	methods: {
		fanhui(){
			this.$router.go(-1);
		},
		subdata(){
			return { 
				goodschashu:{
					cardImg: this.goodslist.cardImg, 
					GoodsTitle: this.goodslist.GoodsTitle, 
					service: this.goodslist.service, 
					Price: this.goodslist.Price,
				},
				xzstate:true,
				goodsNumber:1 
			};
		},
		Submission(){
			this.$store.commit('cartfilter',this.subdata());
			console.log(this.$store.state.ShoppingCart)
		}
	},

	computed:mapState({
		goodslist:"goodstitle"
	}),

//过滤器
filters: {},

mounted() { 
	this.$nextTick(() => { }) 
},

//Vue 实例销毁后调用
destroyed() {
},

components: {},

watch: {}

};


